h1{
	font-family: 'Jokerman', Arial, monospace ;
	color: #9EED00 ;
 }

 @font-face{
 	font-family: 'Jokerman';
 	font-style: normal;
 	font-weight: normal;
  src: local('JOKERMAN'), url(/hashKittens/static/media/JOKERMAN.eec0608f.TTF) format('ttf')
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
   background: -webkit-linear-gradient(left, rgb(7, 112, 101), rgb(7, 58, 135));
   background: linear-gradient(to right, rgb(7, 112, 101), rgb(7, 58, 135));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

